export function getShaderNoiseTexture(): HTMLImageElement | undefined {
  if (typeof window === 'undefined') {
    console.warn('Paper Shaders: can’t create a texture on the server');
    return undefined;
  }

  const img = new Image();
  img.src = noiseSrc;
  return img;
}

const noiseSrc =
  'data:image/png;base64,';
